<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>医生管理</el-breadcrumb-item>
      <el-breadcrumb-item>医生列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row :gutter="20">
        <el-col :span="5">
          <el-select v-model="queryInfo.department" placeholder="科室选择" clearable @clear="getDoctorList">
            <el-option
              v-for="item in departments"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="5">
          <el-select v-model="queryInfo.title" placeholder="职称选择" clearable @clear="getDoctorList">
            <el-option
              v-for="item in titles"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="5">
          <el-input placeholder="医生姓名" v-model="queryInfo.name" clearable @clear="getDoctorList">
            <el-button slot="append" icon="el-icon-search"
                       @click="getDoctorList1"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true">添加医生</el-button>
        </el-col>
      </el-row>

      <el-table :data="doctorList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="职工号" prop="employeeNum"></el-table-column>
        <el-table-column label="姓名" prop="name"></el-table-column>
        <el-table-column label="密码" prop="password"></el-table-column>
        <el-table-column label="电话" prop="tel"></el-table-column>
        <el-table-column label="科室" prop="department"></el-table-column>
        <el-table-column label="职称" prop="title"></el-table-column>
        <el-table-column label="操作" width="170px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.doctorId)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.doctorId)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change=
          "handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    <!--添加医生-->
    <el-dialog
      title="添加医生"
      :visible.sync="addDialogVisible"
      width="30%"
      @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="职工号" prop="employeeNum">
          <el-input v-model="addForm.employeeNum"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="科室" prop="department">
          <el-input v-model="addForm.department"></el-input>
        </el-form-item>
        <el-form-item label="职称" prop="title">
          <el-input v-model="addForm.title"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addDoctor">确 定</el-button>
  </span>
    </el-dialog>
    <!--修改医生信息-->
    <el-dialog
      title="修改医生信息"
      :visible.sync="editDialogVisible"
      width="30%"
      @close="editDialogClosed">
      <el-form :model="editForm" ref="editFormRef" label-width="70px">
        <el-form-item label="职工号" prop="employeeNum">
          <el-input v-model="editForm.employeeNum"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="editForm.password"></el-input>
        </el-form-item>
        <el-form-item label="科室" prop="department">
          <el-input v-model="editForm.department"></el-input>
        </el-form-item>
        <el-form-item label="职称" prop="title">
          <el-input v-model="editForm.title"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editDtoctorInfo">确 定</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Doctor',
  data () {
    return {
      // 获取用户列表参数对象
      queryInfo: {
        name: '',
        department: '',
        title: '',
        // 当前页数
        pagenum: 1,
        pagesize: 2
      },
      doctorList: [],
      total: 0,
      departments: [{
        value: '心理科',
        label: '心理科'
      }, {
        value: '眼科',
        label: '眼科'
      }],
      titles: [{
        value: '专家',
        label: '专家'
      }, {
        value: '护士',
        label: '护士'
      }],
      // 控制添加设备对话框的隐藏
      addDialogVisible: false,
      addForm: {
        employeeNum: '',
        name: '',
        password: '',
        department: '',
        title: ''
      },
      addFormRules: {
        employeeNum: [
          { required: true, message: '请输入职工号', tigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入姓名', tigger: 'blur' },
          { min: 1, max: 5, message: '姓名长度在1-5个字符之间', tigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', tigger: 'blur' },
          { min: 6, max: 15, message: '密码长度在6~15个字符之间', tigger: 'blur' }
        ]
      },
      editDialogVisible: false,
      // 查询到的用户信息对象
      editForm: {}
    }
  },
  created () {
    this.getDoctorList()
  },
  methods: {
    async getDoctorList () {
      console.log(this.queryInfo)
      const { data: res } = await this.$http.get('/doctor/manager/listDoctor', { params: this.queryInfo })
      if (res.code !== 200) {
        return this.$meaasge.error('获取医生列表失败')
      }
      this.doctorList = res.data
      this.total = res.totalpage
      console.log(res)
    },
    // 监听pagesize改变的事件
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getDoctorList()
    },
    // 监听 页码值 改变的事件
    handleCurrentChange (newPage) {
      // console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getDoctorList()
    },
    getDoctorList1 () {
      this.queryInfo.pagenum = 1
      this.getDoctorList()
    },
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    addDoctor () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
        }
        const { data: res } = await this.$http.post('/doctor/manager/addDoctor', this.addForm)
        if (res.code !== 200) {
          this.$message.error(res.msg)
        }
        this.$message.success(res.msg)
        this.addDialogVisible = false
        this.getDoctorList1()
        // console.log(this.addForm)
      })
    },
    async showEditDialog (id) {
      console.log(id)
      const { data: res } = await this.$http.get('/doctor/manager/doctorInfo/' + id)
      if (res.code !== 200) {
        return this.$message.error(res.msg)
      }
      this.editForm = res.data
      console.log(res.data)
      this.editDialogVisible = true
    },
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    async editDtoctorInfo (id) {
      const { data: res } = await this.$http.post('/doctor/manager/editDoctor', this.editForm)
      if (res.code !== 200) {
        return this.$message.error(res.msg)
      }
      this.editDialogVisible = false
      this.getDoctorList()
      this.$message.success(res.msg)
    },
    async removeUserById (id) {
      // 弹框询问
      const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => {
        return err
      })

      // 如果用户确认删除，则返回值为字符串 confirm
      // 如果用户取消删除，则返回值为字符串 cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }

      const { data: res } = await this.$http.delete('/doctor/manager/delete/' + id)
      if (res.code !== 200) {
        return this.$message.error(res.msg)
      }
      this.$message.success(res.msg)
      this.getDoctorList1()
    }
  }
}
</script>

<style lang="less" scoped>

</style>
